import React from "react";
import movieDatas from "../movieDatas.json";
import HighMovie from "../components/HighMovie";
import Movie from "../components/Movie";
import "./Home.css";

class Home extends React.Component {
  state = {
    movies: [],
    highMovies: [],
  };

  getMovies = async () => {
    const movies = movieDatas.movies;
    const moviesCopy = movies.slice();
    this.setState({ movies: movies });
    this.getHighMovies(moviesCopy);
  };

  getHighMovies(movies) {
    const arrSorted = movies.sort((a, b) => {
      const avgA = (a.bbs.rate + a.dys.rate) / 2;
      const avgB = (b.bbs.rate + b.dys.rate) / 2;
      return avgB - avgA;
    });
    const highMovies = arrSorted.slice(0, 10);
    this.setState({ highMovies: highMovies });
  }

  componentDidMount() {
    this.getMovies();
  }

  render() {
    const { movies, highMovies } = this.state;

    return (
      <div className="container">
        <h1 className="nav__title">본 영화</h1>
        <section className="highmovies">
          <h3 className="honor__title">🏆 평점 명예의 전당</h3>
          <div className="highmovies__item__wrap">
            <div className="highmovies__item">
              {highMovies.map((movie) => (
                <HighMovie
                  key={movie.id}
                  id={movie.id}
                  title={movie.title}
                  poster={movie.poster}
                  bgPost={movie.background_post}
                  bgHeader={movie.background_header}
                  bgFooter={movie.background_footer}
                  dateYear={movie.date.year}
                  dateMonth={movie.date.month}
                  dateDay={movie.date.day}
                  genre={movie.genre}
                  duration={movie.duration}
                  preview={movie.preview}
                  bbsRate={movie.bbs.rate}
                  dysRate={movie.dys.rate}
                  bbsReviewTitle={movie.bbs.review_title}
                  dysReviewTitle={movie.dys.review_title}
                  bbsReviewDesc={movie.bbs.review_desc}
                  dysReviewDesc={movie.dys.review_desc}
                />
              ))}
            </div>
          </div>
        </section>

        <section className="movies">
          {movies.map((movie) => (
            <Movie
              key={movie.id}
              id={movie.id}
              title={movie.title}
              poster={movie.poster}
              bgPost={movie.background_post}
              bgHeader={movie.background_header}
              bgFooter={movie.background_footer}
              dateYear={movie.date.year}
              dateMonth={movie.date.month}
              dateDay={movie.date.day}
              genre={movie.genre}
              duration={movie.duration}
              preview={movie.preview}
              bbsRate={movie.bbs.rate}
              dysRate={movie.dys.rate}
              bbsReviewTitle={movie.bbs.review_title}
              dysReviewTitle={movie.dys.review_title}
              bbsReviewDesc={movie.bbs.review_desc}
              dysReviewDesc={movie.dys.review_desc}
            />
          ))}
        </section>
      </div>
    );
  }
}

export default Home;
