import React from "react";
import "./Detail.css";
import DYS_IMG from "../img/dys.png";
import BBS_IMG from "../img/bbs.png";

class Detail extends React.Component {
  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    const {
      location: { state },
      history,
    } = this.props;

    console.log(history);
    if (history.action === "POP") {
      history.replace("/");
    }

    const headerImg = {
      backgroundImage: `url(${state?.bgHeader})`,
    };

    const footerImg = {
      backgroundImage: `url(${state?.bgFooter})`,
    };

    return (
      <div>
        <section className="detail__img" style={headerImg}>
          <div className="gradation__header"></div>
        </section>

        <section className="detail__info">
          <h1 className="detail__title">{state?.title}</h1>
          <div className="detail__subinfo">
            <p>
              개봉:{" "}
              {`${state?.dateYear}년 ${state?.dateMonth}월 ${state?.dateDay}일`}
              <br />
              장르:{" "}
              {state?.genre.map((genre, index) => (
                <strong key={index} className="genres">
                  {genre}
                </strong>
              ))}
              <br />
              상영: {state?.duration}
              <br />
              평점: {(state?.bbsRate + state?.dysRate) / 2}
            </p>
          </div>
        </section>

        <section className="detail__review">
          <article className="review__wrap">
            <div className="user__wrap">
              <img className="user__profile" src={DYS_IMG} alt="user"></img>
              <svg
                width="15"
                height="14"
                viewBox="0 0 15 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M7.21468 0.878116C7.30449 0.601722 7.69551 0.601722 7.78532 0.878115L9.1165 4.97508C9.15666 5.09868 9.27185 5.18237 9.40182 5.18237H13.7096C14.0002 5.18237 14.1211 5.55426 13.886 5.72508L10.4009 8.25714C10.2957 8.33353 10.2517 8.46894 10.2919 8.59255L11.6231 12.6895C11.7129 12.9659 11.3965 13.1957 11.1614 13.0249L7.67634 10.4929C7.57119 10.4165 7.42881 10.4165 7.32366 10.4929L3.83858 13.0249C3.60347 13.1957 3.28712 12.9659 3.37693 12.6895L4.70811 8.59255C4.74827 8.46894 4.70428 8.33353 4.59913 8.25714L1.11405 5.72508C0.878932 5.55426 0.999764 5.18237 1.29038 5.18237H5.59818C5.72815 5.18237 5.84334 5.09868 5.8835 4.97508L7.21468 0.878116Z"
                  fill="#9e9ea4"
                />
              </svg>
              <p className="user__rate">{state?.dysRate}/5</p>
            </div>
            <header>{state?.dysReviewTitle}</header>
            <p>{state?.dysReviewDesc}</p>
          </article>

          <article className="review__wrap">
            <div className="user__wrap">
              <img className="user__profile" src={BBS_IMG} alt="user"></img>
              <svg
                width="15"
                height="14"
                viewBox="0 0 15 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M7.21468 0.878116C7.30449 0.601722 7.69551 0.601722 7.78532 0.878115L9.1165 4.97508C9.15666 5.09868 9.27185 5.18237 9.40182 5.18237H13.7096C14.0002 5.18237 14.1211 5.55426 13.886 5.72508L10.4009 8.25714C10.2957 8.33353 10.2517 8.46894 10.2919 8.59255L11.6231 12.6895C11.7129 12.9659 11.3965 13.1957 11.1614 13.0249L7.67634 10.4929C7.57119 10.4165 7.42881 10.4165 7.32366 10.4929L3.83858 13.0249C3.60347 13.1957 3.28712 12.9659 3.37693 12.6895L4.70811 8.59255C4.74827 8.46894 4.70428 8.33353 4.59913 8.25714L1.11405 5.72508C0.878932 5.55426 0.999764 5.18237 1.29038 5.18237H5.59818C5.72815 5.18237 5.84334 5.09868 5.8835 4.97508L7.21468 0.878116Z"
                  fill="#9e9ea4"
                />
              </svg>
              <p className="user__rate">{state?.bbsRate}/5</p>
            </div>
            <header>{state?.bbsReviewTitle}</header>
            <p>{state?.bbsReviewDesc}</p>
          </article>
        </section>

        <a
          className="preview__link"
          href={state?.preview}
          target="_blank"
          rel="noreferrer"
        >
          <svg
            width="25"
            height="20"
            viewBox="0 0 25 20"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <rect
              x="0.7"
              y="0.7"
              width="23.6"
              height="18.6"
              rx="2.3"
              stroke="#ffcd1a"
              stroke-width="1.4"
            />
            <path
              d="M9 13.9175V6.08255C9 5.61129 9.51837 5.32398 9.918 5.57375L16.1859 9.4912C16.5619 9.7262 16.5619 10.2738 16.1859 10.5088L9.918 14.4263C9.51837 14.676 9 14.3887 9 13.9175Z"
              fill="#ffcd1a"
            />
          </svg>
          <p>예고편 보러 가기</p>
        </a>

        <section className="detail__img" style={footerImg}>
          <div className="gradation__footer"></div>
        </section>
      </div>
    );
  }
}

export default Detail;
